}

.header-big {
  padding-bottom: 75px;
}

.header-top .name {
  font-size: 46px;
  padding: 0 30px;
}

.header-top .logo {
  background-repeat: no-repeat;
  background-position: center;
  width: 63px;
  height: 83px;
  display: block;
}

.header-top {
  padding: 11px 0;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.header-top span {
  font-weight: 700;
}

.header-col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
  display: flex;
  align-items: center;
}

.header-left p {
  max-width: 270px;
  font-size: 14px;
  line-height: 22px;
}

.header-left img {
  padding-left: 23px;
  padding-right: 39px;
}

.header-right {
  justify-content: flex-end;
  font-size: 14px;
}

.header-right div {
  padding-left: 30px;
}

.header-right div:first-child {
  padding-left: 0px;
}

.header-right span {
  padding-left: 4px;
}

.header-right .time {
  color: #a1543e;
  padding-left: 60px;
  position: relative;
}

.header-right .time:before {
  position: absolute;
  top: -1px;
  left: 33px;
  content: '';
  width: 19px;
  height: 19px;
  background: url("../img/time.png") center no-repeat;
}

.header-right .temp {
  color: #a1543e;
  padding-left: 39px;
  position: relative;
}

.header-right .temp:before {
  position: absolute;
  top: -1px;
  left: 19px;
  content: '';
  width: 13px;
  height: 21px;
  background: url("../img/temp.png") center no-repeat;
}

.header-wrapper {
  border-bottom: 1px solid #e3e5e9;
}

a {
  color: #4a90e2;
  text-decoration: none;
}

a:visited {
  color: #4a90e2;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.container.narrow {
  padding-right: 160px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.main-nav {
  position: relative;
  margin-top: 15px;
}

.main-nav a {
  padding-right: 60px;
}

.main-nav #drop {
  display: none;
  margin-left: auto;
  padding-right: 0;
}

.sub-menu {
  display: none;
  position: absolute;
  margin-top: 35px;
  left: 0;
  flex-wrap: wrap;
}

.sub-menu a {
