  margin-top: 0;
}

h1 {
  font-size: 66px;
}

h4 {
  font-size: 20px;
  font-weight: 500;
}

.breadcrumbs a {
  margin-right: 50px;
  position: relative;
}

.breadcrumbs a:after {
  content: '/';
  position: absolute;
  right: -28px;
}

.breadcrumbs a:last-child:after {
  display: none;
}

.content {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(75% - 30px);
  background: #fff;
  padding: 56px 58px 45px 87px;
}

.content-left {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(60% - 30px);
}

.content-left p {
  font-size: 16px;
  line-height: 26px;
}

.content-right {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(40% - 30px);
  padding-left: 85px;
}

.photo-title {
  font-size: 24px;
  font-weight: 300;
  line-height: 34px;
}

.photo-descr {
  font-size: 16px;
  line-height: 24px;
}

.content.full {
  padding: 36px 135px 49px 34px;
}

.links {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  padding-left: 56px;
}

.links h4 {
  margin-top: 0;
  margin-bottom: 35px;
}

.links ul {
  padding-inline-start: 0;
}

.links ul li {
  list-style: none;
  font-size: 16px;
  line-height: 18px;
  padding-bottom: 30px;
}

.links ul li:last-child {
  padding-bottom: 0;
}

.links .active {
  color: #000;
}

.file {
  display: flex;
  align-items: flex-start;
}

.file svg {
  width: 20px;
  height: 20px;
  margin-right: 14px;
}

.file a {
  max-width: 930px;
}

.file-info {
  color: #8a8a8a;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  padding-left: 14px;
}

table {
  border-spacing: 12.5px 19px;
  margin-left: -12.5px;
  max-width: 971px;
}

table td {
  vertical-align: baseline;
}

table td img {
  height: 222px;
  width: auto;
}

table td p {
  color: #525252;
  font-size: 14px;
  line-height: 21px;
}

.search {
  padding-top: 30px;
  padding-bottom: 40px;
  background: #f1f2f4;
}

.search input {
  width: 1153px;
