.loop {
  border: none;
  background-color: transparent;
  position: absolute;
  width: 30px;
  height: 29px;
  background: url("../img/loop.png") center no-repeat;
  top: 15px;
  right: 60px;
}

.blind {
  max-width: 185px;
  color: #000;
  font-family: "Ubuntu";
  font-size: 21px;
  font-weight: 300;
  line-height: 24px;
  text-decoration: none;
  display: inline-block;
  margin-left: 24px;
}

.flex-centered {
  display: flex;
  align-items: center;
}

.quote {
  background-color: #fff;
}

.author {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  display: flex;
  align-items: flex-start;
}

.author img {
  max-height: 100px;
}

.blockquote {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(66.66667% - 30px);
  display: flex;
  flex-direction: column;
  padding-left: 82px;
}

blockquote {
  position: relative;
  margin: 0;
  padding-bottom: 15px;
}

blockquote:before {
  position: absolute;
  content: '';
  width: 52px;
  height: 45px;
  background: url("../img/quote-left.png") center no-repeat;
  top: 0;
  left: -82px;
}

blockquote:after {
  position: absolute;
  content: '';
  width: 52px;
  height: 45px;
  background: url("../img/quote-left.png") center no-repeat;
  top: 22px;
  right: -50px;
  transform: rotate(180deg);
}

cite {
  font-style: normal;
  color: #000000;
  font-size: 24px;
  font-weight: 300;
  line-height: 34px;
  padding-left: 26px;
}

cite div {
  font-size: 14px;
}

.news-wrap {
  display: grid;
  grid-gap: 22px 30px;
  grid-template-columns: 1fr 1fr 1fr;
}

.news-item {
  display: flex;
  background: #fff;
}

.news-item a {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-width: 217px;
  height: 202px;
  display: block;
}

.news-item__content {
  padding-left: 22px;
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.news-item__content p {
  margin-bottom: 12px;
  font-size: 14px;
  line-height: 18px;
}

.news-item__content h3 {
  margin-top: 14px;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 500;
  color: #4a90e2;
}

.news-item.big {
  grid-column: 1 / 3;
  grid-row: 1 / 3;
}

.news-item.big a {
  min-width: 538px;
  height: 426px;
}

.news-item.big .news-item__content {
  padding-left: 57px;
  padding-right: 64px;
}

.news-item.big .news-item__content p {
  margin-bottom: 44px;
  font-size: 16px;
  line-height: 24px;
}

.news-item.big .news-item__content h3 {
  margin-top: 39px;
  margin-bottom: 18px;
  font-size: 30px;
  font-weight: 500;
  color: #4a90e2;
}

.date {
  color: #a4a4a4;
  font-size: 13px;
  font-weight: 700;
}

.info-title {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(75% - 30px);
  font-size: 40px;
  font-weight: 500;
  padding-top: 311px;
  padding-left: 28px;
}

.info-menu {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  padding-top: 51px;
}

.info-menu nav {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.info-menu nav a {
  color: #fff;
  font-size: 24px;
  font-weight: 300;
  line-height: 64px;
  transition: 0.4s;
}

.info-menu nav a:hover {
  transform: translateX(-20px);
}

.info .container .row {
  background: url("../img/info-bg.jpg") center no-repeat;
  background-size: cover;
  height: 420px;
  color: #fff;
  margin-left: 0;
  margin-right: 0;
}

.block-item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  background: #fff;
  height: 174px;
  margin-bottom: 30px;
  padding-top: 33px;
  padding-left: 43px;
  position: relative;
}

.block-item a {
  font-size: 30px;
  font-weight: 500;
}

.block-item.middle {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px);
  height: 117px;
}

.block-item.big {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
