.block-item.big .arrow {
  bottom: 45px;
}

.block-item.big img {
  padding: 0 90px;
}

.arrow {
  background: url("../img/arrow.png") center no-repeat;
  background-size: cover;
  width: 39px;
  height: 25px;
  position: absolute;
  bottom: 26px;
  right: 30px;
}

.block-item.color {
  background-color: #4a90e2;
  color: #fff;
  padding: 0;
}

.block-item.color a {
  color: #fff;
}

.block-item.image {
  height: 205px;
  display: flex;
  flex-direction: column;
}

.block-item.image .img-wrap {
  height: 100px;
}

.block-item.image a {
  font-size: 20px;
  max-width: 233px;
}

.tar {
  width: 100%;
  text-align: right;
}

.tar a {
  margin-top: 18px;
  font-size: 17px;
  font-weight: 500;
  display: block;
}

.site-item {
  margin-left: 15px;
  margin-right: 125px;
}

nav.sites {
  margin-left: 15px;
  margin-bottom: 27px;
}

nav.sites a {
  padding-bottom: 0px;
  margin-right: 33px;
  border-bottom: dashed 1px #4a90e2;
}

footer {
  padding-top: 60px;
}

footer img {
  padding: 16px 100px 0 100px;
}

.footer-txt {
  font-size: 24px;
  font-weight: 300;
  line-height: 54px;
}

.footer-wrap {
  display: flex;
  align-items: flex-start;
}

.copyrights {
  display: flex;
  justify-content: space-between;
  padding-top: 130px;
  padding-bottom: 40px;
  color: #525252;
  font-size: 12px;
  line-height: 21px;
}

.wide-item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px);
  background: #fff;
  height: 325px;
  margin-bottom: 25px;
  display: flex;
}

.wide-item__img {
  width: 28%;
  height: 100%;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 70px;
}

.wide-item h3 {
  max-width: 800px;
  font-size: 30px;
  font-weight: 500;
  margin-top: 38px;
  margin-bottom: 18px;
}

.wide-item p {
  max-width: 700px;
  margin-bottom: 50px;
  margin-top: auto;
  font-size: 16px;
  line-height: 24px;
}

.wide-item div {
  display: flex;
  flex-direction: column;
}

.wide-item:last-child {
  margin-bottom: 0;
}

.disabled {
  opacity: 0.2;
}

.pagination {
  display: flex;
  align-items: center;
  margin-top: 70px;
}

.pagination a {
  display: flex;
  align-items: center;
  font-size: 16px;
}

.pagination a svg {
  margin-left: 15px;
  transform: translateY(1px) rotate(0deg);
}

.pagination a .left-arr {
  margin-left: 0;
  margin-right: 15px;
}

.page {
  width: 48px;
  height: 40px;
  border-radius: 5px;
  background-color: #ffffff;
  font-size: 18px;
  line-height: 22px;
  color: #000;
  border: none;
  margin-right: 8px;
  margin-left: 8px;
}

.page:first-child {
  margin-left: 16px;
}

.page:last-child {
  margin-right: 16px;
}

.page:hover {
  cursor: pointer;
  background-color: #4a90e2;
  color: #fff;
  transition: 0.4s;
}

.page.active {
  background-color: #4a90e2;
  color: #fff;
}

