* {
	box-sizing: border-box; }

body {
	padding: 0;
	background-color: $bg;
	font-family: 'Ubuntu', sans-serif;
	font-size: 16px; }

.wrapper {
	@include wrapper(); }

.wrapper-fluid {
	@include wrapper-full(); }

.debug {
	@include debug(rgba(0, 0, 0, 0.3), 1px solid rgb(19, 138, 92)); }
